* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: #fdfeff;
  text-align: center;
  font-weight: lighter;
  font-family: "tajawal";
}

main {
  background: url("./images/cover.png"), linear-gradient(#181818, #0b0b0b);
  background-position: top;
  background-size: 100%;
  background-repeat: no-repeat;
}
.header {
  background-image: url("./images/header.png");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  padding: 200px 0;
  margin: 0 auto;
}
.header h1 {
  font-size: 3em;
  padding-right: 45px;
  padding-top: 15px;
}
.header p {
  max-width: 900px;
  margin: 0 auto;
  width: 90%;
  font-size: 22px;
}
.welcome h2 {
  font-size: 45px;
}
.welcome p {
  font-size: 24px;
}
.welcome p:first-of-type {
  margin: 10px 0;
}
.main_container {
  max-width: 920px;
  margin: 0 auto;
  margin-top: 80px;
  padding: 0 20px;
}
.documents {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.submit {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 40%;
  max-width: 300px;
  cursor: pointer;
  font-size: 24px;
  outline: none;
  border: none;
  padding: 15px 0em;
  border-radius: 20px;
  background-color: #c22735;
  margin-top: 30px;
}
.btns {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
.btns_app {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor: pointer;
  width: 45%;
  margin: 0 10px;
  margin-top: 50px;
  padding: 15px 0;
  border: none;
  border-radius: 15px;
  outline: none;
  font-size: 20px;
  background-color: #c22735;
}
.form {
  box-sizing: border-box;
  width: 90%;
  max-width: 525px;
  padding: 60px 10px 20px 10px;
  border-radius: 2em;
  margin: 0 auto;
  background-color: #efefef;
}

.form p,
.form label {
  color: black;
  font-size: 20px;
  text-align: right;
}
.text_input {
  width: 100%;
  margin-top: 5px;
  padding: 15px 10px;
  border: none;
  border-radius: 10px;
  font-size: 18px;
  outline: none;
  color: black;
  background-color: #a1a1a1;
  text-align: right;
}
.app_container,
.form p {
  width: 90%;
  margin: 0 auto;
}
.app_container,
.app_container div {
  margin: 20px auto;
  text-align: right;
}
@media only screen and (max-width: 600px) {
  html,
  body {
    overflow-x: hidden;
  }
  .main_container {
    width: 95%;
    margin: 0 auto;
  }
  .header h1 {
    font-size: 10vw;
    padding-right: 7vw;
  }

  .welcome {
    max-width: 610px;
    width: 80%;
    margin: 0 auto;
    margin-top: -14vw;
    margin-bottom: 15vw;
  }
  .welcome h2 {
    font-size: 6vw;
  }
  .welcome p {
    font-size: 4vw;
  }
  .header {
    padding: 35vw 0;
    transform: translateX(25px);
    width: 94%;
  }
  .btns_app {
    width: 90%;
    margin: 6px 0;
    padding: 10px 0;
    font-size: 4vw;
  }
  .form {
    padding-top: 40px;
  }
  .form p,
  .form label {
    color: black;
    font-size: 4.2vw;
    text-align: right;
  }
  .text_input {
    padding: 12px 10px;
  }
}
